.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1.5vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.characters {
  list-style: none;
  padding-left: 0;
}

.characters li {
  display: flex;
  align-items: center;
  border: solid 2px #d0d0d0;
  border-radius: 12px;
  /* padding: 0.5em 0.8em 0.5em 0.5em; */
  margin-bottom: 1em;
  background: #ffffff;
  width: 285px;
  height: 70px;
  left: 0px;
  top: 0px;
}

.label {
  display: flex;
  font-weight: 100;
  max-width: 60px;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 14px;
  color: #a5adba;
}

.border0 {
  background: #00875a;
  width: 11px;
  height: 70px;
  left: 0px;
  top: 0px;
  border-radius: 12px 0px 0px 12px;
  /* display: flex; */
}
.border1 {
  background: #5e9047;
  width: 10px;
  height: 70px;
  left: 0px;
  top: 0px;
  border-radius: 12px 0px 0px 12px;
  /* display: flex; */
}
.border2 {
  background: #c09934;
  width: 10px;
  height: 70px;
  left: 0px;
  top: 0px;
  border-radius: 12px 0px 0px 12px;
  display: flex;
}
.border3 {
  background: #fd9f28;
  width: 10px;
  height: 70px;
  left: 0px;
  top: 0px;
  border-radius: 12px 0px 0px 12px;
  display: flex;
}
