  background: #de350b;
  width: 10px;
  height: 70px;
  left: 0px;
  top: 0px;
  border-radius: 12px 0px 0px 12px;
  display: flex;
}

.characters p {
  max-width: 250px;
  font-weight: bold;
  margin: 0;
  color: #444a58;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  display: flex;
  align-items: center;
  height: 38px;
  left: 0%;
  right: 0%;
  top: calc(50% - 38px / 2);
}

.characters-thumb {
  overflow: hidden;
  flex-shrink: 0;
  width: 2em;
  height: 2em;
  background-color: #e8e8e8;
  padding: 0.5em;
  margin-right: 0.5em;
}

.characters-thumb img {
  display: block;
  width: 100%;
  height: auto;
}

.number-list {
  text-align: right;
  padding-bottom: 20px;
}
.number-list p {
  padding: 17px 30px 0px 0;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  justify-content: center;
  align-items: center;
}
.button-next {
  position: relative;
  left: 100px;
  bottom: 50px;
  background: #005589;
  border-radius: 4px;
  width: 132px;
  height: 40px;
}

.line {
  justify-content: center;
  position: relative;
  right: 40px;
}

.layout {
  background: #f5f8fa;
}
.instruction {
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: #a5adba;
}
.container {
  display: flex;
  padding: 0 0 50px 0;
}

.border21 {
  opacity: 0.6;
  border: 2px dashed #00875a;
  box-sizing: border-box;
}

.characters2 li {
  display: flex;
  align-items: center;
  opacity: 0.6;
  border: 2px dashed #00875a;
  box-sizing: border-box;
  box-shadow: inset 0px 0px 12px rgba(0, 0, 0, 0.06);
  border-radius: 12px;
  /* padding: 0.5em 0.8em 0.5em 0.5em; */
  margin-bottom: 1em;
  background: #ffffff;
  width: 285px;
  height: 70px;
  left: 0px;
  top: 0px;
}

